import React, { Component } from 'react';
const colors=['#F2F2F2','#F0ECEC','#8B8989','#BF6666','#F2C6C6','#F4A3A3','#D0CBCA','#F4F3F0','#F8F8E6']
export default  class Resume extends Component {
  constructor(props){
    super(props)
    this.state={color:'',
                prevPos:window.pageYOffset    }
  }
  getColor(){
    const pos=Math.floor(Math.random() * colors.length)
    return  colors[pos]
  }
  handleScroll=()=>{
  const {prevPos}=this.state 
  const currentPos=window.pageYOffset
    if(Math.abs(currentPos-prevPos)>30){
      const color=this.getColor()
      this.setState({prevPos:currentPos,color:color})
    }

  }
  componentDidMount(){
    window.addEventListener('scroll',this.handleScroll)
  }
  render() {
    let resumeData = this.props.resumeData;  // import resumeData.js
    let {color}=this.state
    return (
      <section id="resume" >
      <div className="row">
        <ul className="intro">
          <li>
            <a className="smoothscroll" href="#skill_id">Skills</a>
          </li>
          <li>
            <a className="smoothscroll" href="#work_id">Work</a>
          </li>
          <li>
            <a className="smoothscroll" href="#education_id">Education</a>
          </li>
          <li>
            <a className="smoothscroll" href="#project_id">Project</a>
          </li>
        </ul>
      </div>

      <div id="skill_id"/>
      <div className="row skill">
        <div className="three columns header-col">
           <h1><span>Skills</span></h1>
        </div>
        <div className="nine columns main-col">
          <div className="bars">
                <ul className="skills">
                  {
                    resumeData.skills && resumeData.skills.map((item) => {
                      return(
                      <ul><h4>{item.skilltype}</h4>
                        <li>
                        {item.skillname}
                        </li>
                      </ul>
                      )
                    })
                  }
                </ul>
            </div>
        </div>
      </div>

      <div id="work_id"/>
      <div className="row work">
          <div className="three columns header-col">
            <h1>
              <span>Work</span>
            </h1>
          </div>
          <div className="nine columns main-col">
            {
              resumeData.work && resumeData.work.map((item) => {
                return(
                  <div className="row item">
                     <div className="twelve columns">
                        <h3>{item.CompanyName}</h3>
                        <p className="info">
                        {item.specialization}
                        <span>&bull;</span> <em className="date"> {item.MonthOfStarting} {item.YearOfStarting} - {item.MonthOfLeaving} {item.YearOfLeaving}</em></p>
                        <p className="job_description">
                        {item.Achievements.map(ite=>{
                          return(<ul>
                            <li>{ite}</li>
                          </ul>)
                          
                        })}
                        </p>
                     </div>

                  </div>

                )
              })
            }
          </div> 
      </div>

      <div id="education_id"/>
       <div className="row education">

          <div className="three columns header-col">
             <h1><span>Education</span></h1>
          </div>

          <div className="nine columns main-col">
            {
              resumeData.education && resumeData.education.map((item)=>{
                return(
                  <div className="row item">
                     <div className="twelve columns">
                        <h3>{item.Degree}</h3>
                        <a className="schoolLink" href={item.url} target="_blank">
                        {item.UniversityName}
                        </a>
                        <p className="info">
                        {item.specialization}
                        <span>&bull;</span> <em className="date">{item.YearOfPassing}</em></p>
                        <p>
                        {item.Achievements}
                        </p>
                     </div>
                  </div>
                )
              })
            }
          </div>
       </div>

       <div id="project_id"/>
       <div className="row project">

          <div className="three columns header-col">
             <h1><span>Project</span></h1>
          </div>

          <div className="nine columns main-col">
            {
              resumeData.project && resumeData.project.map((item)=>{
                return(
                  <div className="row item">
                     <div className="twelve columns">
                        <a href={item.url} target="_blank">
                        <h2 className="projectName">{item.projectName}</h2></a>
                        <h4>{item.projectContent}</h4>
                        <p className="info">
                        <span>&bull;</span> <em className="date">{item.YearOfPassing}</em> </p>
                        <p className="project_description">
                        {item.projectDescription.map(ite=>{
                          return(<ul>
                            <li>{ite}</li>
                          </ul>)
                        })}
                        </p>
                        <p id="achievement">{item.projectAchievement}</p>
                     </div>
                  </div>
                )
              })
            }
          </div>
       </div>

      </section>
    );
  }
}